



























































































import Vue from 'vue';
import UserControl, { User } from '../store/UserModule';
import StatusControl from '../store/StatusModule';
import { Component, Prop, Watch } from 'vue-property-decorator';

@Component
export default class Signup extends Vue {
	// Data
	private email: string = '';
	private password: string = '';
	private confirmEmail: string = '';
	private confirmPassword: string = '';
	private firstName: string = '';
	private lastName: string = '';

	// Computed
	get compareEmails() {
		return this.email !== this.confirmEmail ? 'Emails do not match' : true;
	}
	get comparePasswords() {
		return this.password !== this.confirmPassword
			? 'Passwords do not match'
			: true;
	}
	get user() {
		return UserControl.getUser;
	}
	get error() {
		return StatusControl.getError;
	}
	get loading() {
		return StatusControl.isLoading;
	}

	@Watch('user')
	public onSuccess(item: User | null) {
		if (item) {
			this.$router.push('/');
		}
	}

	// Methods
	public async onSignup() {
		const newUser = new User();
		newUser.firstName = this.firstName;
		newUser.lastName = this.lastName;

		UserControl.signUserUp({
			email: this.email,
			password: this.password,
			user: newUser,
		});
	}
	public onDismissed() {
		// Error dismissed
		StatusControl.setError(null);
	}
}
