import { auth, database } from 'firebase';
import UserControl, { User } from '../store/UserModule';
import StatusControl from '../store/StatusModule';
import CustomError from '../classes/CustomError';
import FirebaseAccount from './Account';
import FirebaseDB, { createPath as path } from '../classes/Database';

export interface OrgStructure {
	admins: { [uid: string]: boolean };
	members: { [uid: string]: boolean };
	events?: {};
	meta: {
		publicEvents: boolean;
		description: string;
		logoSrc: string;
	};
}

export interface OrgAnnouncementStructure {
	message: string;
	meta: {
		timestamp: string;
		author: string;
	};
}

export const OrgDefaultLogo =
	'https://upload.wikimedia.org/\
		wikipedia/en/thumb/d/d1/University_of_South_Florida_seal.svg/\
		1200px-University_of_South_Florida_seal.svg.png';

export enum OrgAccessLevel {
	Admin = 'Admin',
	Member = 'Member',
	NonMember = 'Non-member',
}

const defaultStructure: OrgStructure = {
	admins: {},
	members: {},
	meta: {
		publicEvents: false,
		description: '',
		logoSrc: '',
	},
};

export default class OrgActions {
	public static readonly defaultStructure = defaultStructure;
	public static readonly OrgAccessLevel = OrgAccessLevel;

	public static async exists(orgName: string): Promise<boolean> {
		try {
			this.preChecks();
			this.preCheckOrgName(orgName);

			StatusControl.setLoading(true);
			if (orgName.length <= 0) {
				StatusControl.setError(
					new CustomError(null, 'No empty organization exists.'),
				);
				return false;
			}

			return await FirebaseDB.exists(path(this.orgPath, orgName));
		} catch (error) {
			StatusControl.setError(error);
			return false;
		} finally {
			StatusControl.setLoading(false);
		}
	}

	// Attempt to add new organization
	public static async add(
		orgName: string,
		overrideIfExists: boolean = false,
		orgData: OrgStructure,
	): Promise<firebase.database.Reference | null> {
		try {
			StatusControl.setLoading(true);

			const user = this.preChecks();
			this.preCheckOrgName(orgName);

			if ((await this.exists(orgName)) && !overrideIfExists) {
				StatusControl.setError(
					new CustomError(null, 'This organization already exists.'),
				);
				return null;
			}

			// Attempts to add new org with current user - subject to permissions
			const orgRef = database().ref(path(this.orgPath, orgName));
			await FirebaseDB.set(orgRef, orgData);

			// Add org to each user's profile
			if (orgData.admins) {
				for (const key of Object.keys(orgData.admins)) {
					await FirebaseDB.set(
						path(this.userPath, key, 'orgs', orgName),
						true,
					);
				}
			}

			if (orgData.members) {
				for (const key of Object.keys(orgData.members)) {
					await FirebaseDB.set(
						path(this.userPath, key, 'orgs', orgName),
						true,
					);
				}
			}

			return orgRef;
		} catch (error) {
			StatusControl.setError(new CustomError(error));
			return null;
		} finally {
			StatusControl.setLoading(false);
		}
	}

	// Attempts to add announcement to given organization and returns the unique id of it
	public static async addAnnouncement(
		announcement: OrgAnnouncementStructure,
		orgName: string,
		isPublic: boolean = false,
	): Promise<string | null> {
		try {
			StatusControl.setLoading(true);
			const user = this.preChecks();
			this.preCheckOrgName(orgName);

			// Check if user has permission to add announcements
			if (
				!user.isAdmin &&
				!(
					(await this.isMemberOf(orgName, user.uid)) ===
					OrgAccessLevel.Admin
				)
			) {
				throw new CustomError(
					null,
					'Must be an admin to write announcements.',
				);
			} else if (announcement.message.length <= 0) {
				throw new CustomError(
					null,
					'Announcement must have a message.',
				);
			}

			// Push to announcements container folder
			const key = await FirebaseDB.push(
				path(this.orgAnnouncementsPath, orgName),
				announcement,
			);

			// Check if key is valid
			if (!key) {
				throw new CustomError(
					null,
					'Key returned after announcement was added is not valid.',
				);
			}

			// Add this key to the organizations page
			await FirebaseDB.set(
				path(this.orgPath, orgName, 'announcements', key),
				isPublic,
			);

			return key;
		} catch (error) {
			StatusControl.setError(error);
			return null;
		} finally {
			StatusControl.setLoading(false);
		}
	}

	public static async getAnnouncements(
		orgName: string,
	): Promise<OrgAnnouncementStructure[]> {
		try {
			StatusControl.setLoading(true);
			const user = this.preChecks();
			this.preCheckOrgName(orgName);

			// Get all announcement metadata
			const announcements: {
				[pushID: string]: boolean;
			} = await FirebaseDB.get(
				path(this.orgPath, orgName, 'announcements'),
			);

			// Get all announcements that the user has permission to read
			const readableAnnouncements: OrgAnnouncementStructure[] = [];
			for (const pushID of Object.keys(announcements)) {
				if (
					user.isAdmin ||
					announcements[pushID] ||
					(await this.isMemberOf(orgName, user.uid))
				) {
					// Get full announcement data
					const curr = await FirebaseDB.get(
						path(this.orgAnnouncementsPath, orgName, pushID),
					);

					// Push to array
					readableAnnouncements.push(curr);
				}
			}

			return readableAnnouncements;
		} catch (error) {
			StatusControl.setError(error);
			return [];
		} finally {
			StatusControl.setLoading(false);
		}
	}

	public static async deleteOrg(orgName: string) {
		try {
			StatusControl.setLoading(true);
			const user = this.preChecks();
			this.preCheckOrgName(orgName);

			if (!this.exists(orgName)) {
				throw new CustomError(
					null,
					'This organization does not exist.',
				);
			} else if (!user.isAdmin) {
				throw new CustomError(null, 'Must be an admin to do this.');
			}

			const orgRef = database()
				.ref(this.orgPath)
				.child(orgName);

			const orgRawData = await orgRef.once('value');
			const orgData: OrgStructure = orgRawData.val();

			// Remove all references to current org from members
			if (orgData.admins) {
				for (const key of Object.keys(orgData.admins)) {
					await FirebaseDB.remove(path('user', key, 'orgs', orgName));
				}
			}

			if (orgData.members) {
				for (const key of Object.keys(orgData.members)) {
					await FirebaseDB.remove(path('user', key, 'orgs', orgName));
				}
			}

			// Then remove the org itself
			await FirebaseDB.remove(orgRef);
		} catch (error) {
			StatusControl.setError(new CustomError(error));
		} finally {
			StatusControl.setLoading(false);
		}
	}

	public static async addUserToOrg(
		orgName: string,
		uidToAdd: string,
		accessLevel: OrgAccessLevel,
	) {
		try {
			StatusControl.setLoading(true);
			const user = this.preChecks();
			this.preCheckOrgName(orgName);

			if (!this.exists(orgName)) {
				throw new CustomError(null, 'Organization does not exist.');
			} else if (
				!user.isAdmin &&
				(await this.isMemberOf(orgName, user.uid)) !==
					OrgAccessLevel.Admin
			) {
				throw new CustomError(null, 'Must be an admin to do this.');
			}

			const orgRef = await database()
				.ref('org')
				.child(orgName);

			const userOrgRef = database()
				.ref('user')
				.child(uidToAdd)
				.child('orgs');

			const adminRef = orgRef.child('admins').child(uidToAdd);
			const memberRef = orgRef.child('members').child(uidToAdd);

			if (accessLevel === OrgAccessLevel.Admin) {
				// Adding user to admins of org

				// Check if already an admin
				if (await FirebaseDB.exists(adminRef)) {
					// Already exists
					return; // Do nothing
				}

				// Add to admins
				await FirebaseDB.set(adminRef, true);

				// Remove from members if exists
				if (await FirebaseDB.exists(memberRef)) {
					await FirebaseDB.remove(memberRef);
				}

				// Add org to user's profile if it doesn't exist
				if (!(await FirebaseDB.exists(userOrgRef.child(orgName)))) {
					await FirebaseDB.set(userOrgRef.child(orgName), true);
				}
			} else if (accessLevel === OrgAccessLevel.Member) {
				// Adding user to members of org

				// Check if already a member
				if (await FirebaseDB.exists(memberRef)) {
					// Already exists
					return; // Do nothing
				}

				// Push back updated member array with user now in it
				await FirebaseDB.set(memberRef, true);

				// Remove from admin if exists
				if (await FirebaseDB.exists(adminRef)) {
					await FirebaseDB.remove(adminRef);
				}

				// Add org to user's profile if it doesn't exist
				if (!(await FirebaseDB.exists(userOrgRef.child(orgName)))) {
					await FirebaseDB.set(userOrgRef.child(orgName), true);
				}
			} else if (accessLevel === OrgAccessLevel.NonMember) {
				// Check if user is a member
				if (await FirebaseDB.exists(memberRef)) {
					// Already exists
					await FirebaseDB.remove(memberRef);
				}

				// Check if user is an admin
				if (await FirebaseDB.exists(adminRef)) {
					// Already exists
					await FirebaseDB.remove(adminRef);
				}

				// Remove org from user's orgs if it exists
				if (await FirebaseDB.exists(userOrgRef.child(orgName))) {
					// Already exists
					await FirebaseDB.remove(userOrgRef.child(orgName));
				}
			} else {
				throw new CustomError(
					null,
					'Unrecognized access level provided: ' + accessLevel,
				);
			}
		} catch (error) {
			StatusControl.setError(new CustomError(error));
		} finally {
			StatusControl.setLoading(false);
		}
	}

	public static async getUserOrgs(): Promise<string[]> {
		try {
			StatusControl.setLoading(true);
			const user = this.preChecks();
			const ref = database()
				.ref(this.userPath)
				.child(user.uid)
				.child('orgs');

			if (!(await FirebaseDB.exists(ref))) {
				return [];
			}

			return Object.keys(await FirebaseDB.get(ref));
		} catch (error) {
			StatusControl.setError(new CustomError(error));
			return [];
		} finally {
			StatusControl.setLoading(true);
		}
	}

	public static async getAllOrgs() {
		try {
			this.preChecks();
			StatusControl.setLoading(true);
			const dataRef = await database()
				.ref(this.orgPath)
				.orderByKey()
				.once('value');
			return dataRef.val();
		} catch (error) {
			StatusControl.setError(error);
		} finally {
			StatusControl.setLoading(true);
		}
	}

	public static async isMemberOf(
		orgName: string,
		uid: string,
	): Promise<OrgAccessLevel | null> {
		// Check if user exists in this org
		if (
			await FirebaseDB.exists(path(this.orgPath, orgName, 'admins', uid))
		) {
			// User is an admin
			return OrgAccessLevel.Admin;
		} else if (
			await FirebaseDB.exists(path(this.orgPath, orgName, 'members', uid))
		) {
			// User is a member
			return OrgAccessLevel.Member;
		} else {
			// User is not a member
			return null;
		}
	}

	private static readonly orgPath: string = 'org';
	private static readonly userPath: string = 'user';
	private static readonly orgAnnouncementsPath: string = 'org_announcements';

	private static preCheckOrgName(orgName: string) {
		if (orgName.length <= 0) {
			throw new CustomError(null, 'Organization name cannot be blank.');
		} else if (!orgName) {
			throw new CustomError(
				null,
				'Organization name cannot be undefined.',
			);
		} else if (orgName.replace('/s/g', '').length <= 0) {
			throw new CustomError(
				null,
				'Organization name must contain valid characters.',
			);
		}
	}

	private static preChecks(): User {
		const user = UserControl.getUser;
		if (!user) {
			throw new CustomError(null, 'User not signed in.');
		}

		return user;
	}
}
