











































































































































































import Vue from 'vue';
import { Component, Prop, Watch } from 'vue-property-decorator';
import StatusControl from '../../store/StatusModule';
import CustomError from '../../classes/CustomError';
import UserControl from '../../store/UserModule';
import { OrgStructure, OrgDefaultLogo } from '../../classes/OrgActions';
import { database } from 'firebase';
import { UserStructure } from '../../classes/Database';

type autoCompleteFormat = Array<{
	header?: string;
	name?: string;
	uid?: string;
	avatar?: string;
	divider?: boolean;
}>;

@Component
export default class OrgAdminControls extends Vue {
	// If true, displays the addDialog window
	@Prop({ default: false })
	public addDialog: boolean;

	// If true, displays the delDialog window
	@Prop({ default: false })
	public delDialog: boolean;

	// If true, displays the addUser window
	@Prop({ default: false })
	public addUserDialog: boolean;

	// The user pool that the dialogs will pull from while adding users/orgs
	@Prop({
		default: () => {
			return [];
		},
	})
	private autoCompleteUsers: autoCompleteFormat;

	// Org names the dialogs will pull from
	@Prop({
		default: () => {
			return [];
		},
	})
	private autoCompleteOrgNames: string[];

	// Access levels the dialogs will pull from
	@Prop({
		default: () => {
			return [];
		},
	})
	private autoCompleteAccessLevels: string[];

	// If true, will clear all input
	private clearInput = false;

	private orgName: string = '';
	private orgDesc: string = '';
	private admins: string[] = [];
	private members: string[] = [];
	private publicEvents: boolean = false;
	private logoSrc: string = '';
	private logoSrcConfirmed: string = OrgDefaultLogo;

	private addOrgStepper: number = 1;
	private addOrgMaxSteps: number = 3;

	private autoCompleteAdmins: autoCompleteFormat = [];
	private autoCompleteMembers: autoCompleteFormat = [];

	private selectedUsers: any = [];
	private accessLevel: string[] = [];

	// Checks if the application is loading
	private get loading(): boolean {
		return StatusControl.isLoading;
	}

	// Moves the stepper to the next step while adding organizations
	private nextStep() {
		if (this.admins.length > 0) {
			this.addOrgStepper++;
		}
	}

	// Confirms the image url for the organization
	private selectImage(src: string) {
		if (src === '') {
			this.logoSrcConfirmed = OrgDefaultLogo;
		} else {
			this.logoSrcConfirmed = this.logoSrc;
		}
	}

	// Requires that the string array entered has at least once entry
	// * Used for adding organizations
	private requireAtleastOne(arg: string[]): string | boolean {
		if (arg.length > 0) {
			return true;
		} else {
			return 'At least one admin is required.';
		}
	}

	// Clears all data in this class and sets them to defaults
	private clearData() {
		this.orgName = '';
		this.orgDesc = '';
		this.admins = [];
		this.members = [];
		this.publicEvents = false;
		this.addOrgStepper = 1;
		this.autoCompleteAdmins = [];
		this.autoCompleteMembers = [];
		this.selectedUsers = [];
		this.accessLevel = [];
		this.logoSrc = '';
		this.selectImage('');
		this.clearInput = true;
	}

	// Activated when the addDialog final button is clicked
	private addDialogClicked(bool: boolean) {
		const admins: { [uid: string]: boolean } = {};
		this.admins.forEach((x) => {
			admins[x] = true;
		});

		const members: { [uid: string]: boolean } = {};
		this.members.forEach((x) => {
			members[x] = true;
		});

		const org: OrgStructure = {
			admins,
			members,
			meta: {
				publicEvents: this.publicEvents,
				description: this.orgDesc,
				logoSrc: this.logoSrc,
			},
		};

		this.$emit('addSaved', bool, { orgName: this.orgName, orgData: org });
		this.clearInput = false;
	}

	// Activated when the delDialog final button is clicked
	private delDialogClicked(bool: boolean) {
		this.$emit('delSaved', bool, {
			orgName: this.orgName,
		});
		this.clearInput = false;
	}

	// Activated when the addUser final button is clicked
	private addUserClicked(bool: boolean) {
		this.$emit('userSaved', bool, {
			orgName: this.orgName,
			accessLevel: this.accessLevel,
			selectedUsers: this.selectedUsers,
		});
		this.clearInput = false;
	}

	// Clears current input of dialogs if new dialog is opened
	@Watch('addDialog')
	@Watch('addUserDialog')
	@Watch('delDialog')
	private newDialogShown(newVal: boolean) {
		// If we're opening the add dialog, reset contents
		if (newVal) {
			this.clearData();
		}
	}

	// Filters the selection available from members with the currently selected admins
	@Watch('admins')
	private adminsChanged(newVal: string[], oldVal: string[]) {
		this.autoCompleteMembers = this.autoCompleteUsers.filter((x) => {
			if (!x.uid) {
				return false;
			} else {
				return !newVal.includes(x.uid);
			}
		});
	}

	// Filters the selection available from admins with the currently selected members
	@Watch('members')
	private membersChanged(newVal: string[], oldVal: string[]) {
		this.autoCompleteAdmins = this.autoCompleteUsers.filter((x) => {
			if (!x.uid) {
				return false;
			} else {
				return !newVal.includes(x.uid);
			}
		});
	}

	// Resets the user pool if there is a incoming change to it
	@Watch('autoCompleteUsers')
	private autoCompleteUsersChanged() {
		if (this.admins.length === 0 && this.members.length === 0) {
			this.autoCompleteAdmins = this.autoCompleteMembers = this.autoCompleteUsers;
		}
	}
}
