














































import Vue from 'vue';
import UserControl, { User } from '../store/UserModule';
import Status from '../store/StatusModule';
import { Component, Prop, Watch } from 'vue-property-decorator';

@Component
export default class Login extends Vue {
	// Data
	private email: string = '';
	private password: string = '';

	// Computed
	get user() {
		return UserControl.getUser;
	}
	get error() {
		return Status.getError;
	}
	get loading() {
		return Status.isLoading;
	}

	@Watch('user')
	public onSuccess(item: User | null) {
		if (item) {
			this.$router.push('/');
		}
	}

	// Methods
	public async onLogin() {
		await UserControl.signUserInWithEmailAndPassword({
			email: this.email,
			password: this.password,
		});
	}
	public onDismissed() {
		// Error dismissed
		Status.setError(null);
	}
}
