import { getStoreBuilder, BareActionContext } from 'vuex-type-safety';
import { RootState } from '.';
import CustomError from '../classes/CustomError';

interface StatusState {
	isLoading: boolean;
	error: CustomError | null;
}

const initialstate: StatusState = {
	isLoading: false,
	error: null,
};

const builder = getStoreBuilder<RootState>().module(
	'statusModule',
	initialstate,
);

/// MUTATORS

// Sets the error state
const COMMIT_ERROR = builder.commit(function commitError(
	state: StatusState,
	payload: CustomError | null,
) {
	if (payload) {
		if (!state.error) {
			state.error = new CustomError(null);
		}
		Object.assign(state.error, payload);
	} else {
		state.error = null;
	}
});

// Sets whether the application is currently loading or not
const COMMIT_LOADING = builder.commit(function commitLoading(
	state: StatusState,
	payload: boolean,
) {
	state.isLoading = payload;
});

/// GETTERS

// Returns the current error state
const GetError = builder.read(function getError(state: StatusState) {
	return state.error;
});

// Returns whether the state contains an error
const HasError = builder.read(function hasError(state: StatusState) {
	return state.error != null;
});

// Returns the current state
const GetState = builder.read(function getState(state: StatusState) {
	return state;
});

export interface StatusModule {
	getError: CustomError | null;
	hasError: boolean;
	isLoading: boolean;
	setError(payload: CustomError | null): Promise<void>;
	setLoading(payload: boolean): Promise<void>;
}

/// PROTOTYPE
const status: StatusModule = {
	// Getters
	get getError() {
		return GetError();
	},
	get hasError() {
		return HasError();
	},
	get isLoading() {
		return GetState().isLoading;
	},

	// Actions
	setError: builder.dispatch(async function setError(
		context: BareActionContext<StatusState, RootState>,
		payload: CustomError | null,
	) {
		COMMIT_ERROR(payload);
	}),
	setLoading: builder.dispatch(async function setLoading(
		context: BareActionContext<StatusState, RootState>,
		payload: boolean,
	) {
		COMMIT_LOADING(payload);
	}),
};

export default status;
