import { getStoreBuilder, BareActionContext } from 'vuex-type-safety';
import { RootState } from '.';

import FirebaseAccount from '../classes/Account';
import StatusControl from './StatusModule';
import CustomError from '../classes/CustomError';
import Firebase, { auth, database } from 'firebase';

export class User {
	public firstName: string;
	public lastName: string;
	public isAdmin: boolean;
	public img: string;
	public uid: string;
}

interface UserState {
	user: User | null;
}

const initialstate: UserState = {
	user: null,
};

const builder = getStoreBuilder<RootState>().module('userModule', initialstate);

/// MUTATORS

// Sets the current user
const SET_USER = builder.commit(function commitUser(
	state: UserState,
	payload: User | null,
) {
	if (payload !== null) {
		if (state.user === null) {
			state.user = new User();
		}
		Object.assign(state.user, payload);
	} else {
		CLEAR_USER();
	}
});

const CLEAR_USER = builder.commit(function clearUser(state: UserState) {
	state.user = null;
});

// GETTERS

// Returns the current user
const GetUser = builder.read(function getUser(state: UserState): User | null {
	return state.user;
});

// Returns the current user
const IsUserSignedIn = builder.read(function isUserSignedIn(
	state: UserState,
): boolean {
	return state.user !== null;
});

// Returns the current user
const IsUserAdmin = builder.read(function isUserAdmin(
	state: UserState,
): boolean {
	return state.user ? state.user.isAdmin : false;
});

export interface UserModule {
	getUser: User | null;
	isUserSignedIn: boolean;
	isUserAdmin: boolean;
	setUser(payload: User): Promise<void>;
	signUserInWithEmailAndPassword(payload: {
		email: string;
		password: string;
	}): Promise<void>;
	signInactiveUserIn(payload: { account: Firebase.User }): Promise<void>;
	signUserUp(payload: {
		email: string;
		password: string;
		user: User;
	}): Promise<void>;
	signUserOut(): Promise<void>;
}

/// PROTOTYPE
const userControl: UserModule = {
	// Getters
	get getUser() {
		return GetUser();
	},
	get isUserSignedIn() {
		return IsUserSignedIn();
	},
	get isUserAdmin() {
		return IsUserAdmin();
	},

	// Actions
	setUser: builder.dispatch(async function setUser(
		context: BareActionContext<UserState, RootState>,
		payload: User,
	) {
		SET_USER(payload);
	}),
	signUserInWithEmailAndPassword: builder.dispatch(
		async function signUserInWithEmailAndPassword(
			context: BareActionContext<UserState, RootState>,
			payload: { email: string; password: string },
		) {
			// Attempt to sign in
			try {
				const response = await FirebaseAccount.signInWithEmailAndPassword(
					payload.email,
					payload.password,
				);
				localStorage.setItem(
					'ORGnizer.localUser',
					JSON.stringify(response),
				);
				SET_USER(response);
			} catch (error) {
				StatusControl.setError(error);
			}
		},
	),
	signInactiveUserIn: builder.dispatch(async function signInactiveUserIn(
		context: BareActionContext<UserState, RootState>,
		payload: { account: Firebase.User },
	) {
		const userInfo = await FirebaseAccount.getAccountInfo();
		const userIsAdmin = await FirebaseAccount.isUserAdmin();
		const user = {
			firstName: userInfo.first,
			lastName: userInfo.last,
			uid: payload.account.uid,
			isAdmin: userIsAdmin,
			img: userInfo.img,
		};
		localStorage.setItem('ORGnizer.localUser', JSON.stringify(user));
		SET_USER(user);
	}),
	signUserUp: builder.dispatch(async function signUserUp(
		context: BareActionContext<UserState, RootState>,
		payload: { email: string; password: string; user: User },
	) {
		StatusControl.setLoading(true);
		try {
			const response = await auth().createUserWithEmailAndPassword(
				payload.email,
				payload.password,
			);
			if (!response.user) {
				StatusControl.setError(
					new CustomError(null, 'Signup unsuccessful.'),
				);
				return;
			}

			const accountRef = database().ref('user/' + response.user.uid);
			await accountRef.child('i').update({
				first: payload.user.firstName,
				last: payload.user.lastName,
			});

			await accountRef
				.child('meta')
				.child('isAdmin')
				.set(false);

			localStorage.setItem(
				'ORGnizer.localUser',
				JSON.stringify(payload.user),
			);
		} catch (error) {
			StatusControl.setError(error);
		} finally {
			StatusControl.setLoading(false);
		}
	}),
	signUserOut: builder.dispatch(async function signUserOut(
		context: BareActionContext<UserState, RootState>,
	) {
		await FirebaseAccount.signUserOut();
		localStorage.removeItem('ORGnizer.localUser');
		CLEAR_USER();
	}),
};

export default userControl;
