

























































































































import Vue from 'vue';
import { Component } from 'vue-property-decorator';
import UserControl from './store/UserModule';
import StatusControl from './store/StatusModule';
import CustomError from './classes/CustomError';

@Component
export default class AppComponent extends Vue {
	public clipped = false;
	public drawer = true;
	public fixed = false;

	public miniVariant = true;
	public right = true;
	public title = 'ORGnizer';
	public name = '';

	public fav = false;
	public hints = false;
	public message = false;

	// List of menu items to be displayed
	// See: https://material.io/tools/icons/?style=baseline
	get menuItems() {
		const menuItems = [
			{
				icon: 'home',
				title: 'Home',
				to: '/',
				type: 'button',
				id: 'home',
			},
			{
				icon: 'calendar_today',
				title: 'Calendar',
				to: '/calendar',
				type: 'button',
				id: 'calendar',
			},
			{
				icon: 'school',
				title: 'Organizations',
				to: '/organizations',
				type: 'button',
				id: 'organizations',
			},
		];

		if (!this.userIsAuthenticated) {
			menuItems.push({
				icon: 'lock_open',
				title: 'Login',
				to: '/login',
				type: 'button',
				id: 'login',
			});
		} else {
			menuItems.push({
				icon: 'account_circle',
				title: 'Account',
				to: '/accountpage',
				type: 'dropdown',
				id: 'account',
			});
		}

		return menuItems;
	}

	// Checks that the user is current signed in
	get userIsAuthenticated() {
		return UserControl.isUserSignedIn;
	}

	get getUser() {
		return UserControl.getUser;
	}

	get getLoading() {
		return StatusControl.isLoading;
	}

	get getError() {
		return StatusControl.getError;
	}

	get getErrorTypeToString() {
		if (!StatusControl.getError) {
			return 'info';
		}
		return CustomError.TypeToString(StatusControl.getError.type);
	}

	private dismissError() {
		StatusControl.setError(null);
	}

	// Log current user out
	private logOut() {
		this.$router.push('/');
		UserControl.signUserOut();
	}
}
