




























import Vue from 'vue';
import StatusControl from '../store/StatusModule';
import CustomError from '../classes/CustomError';
import { Component, Prop, Watch } from 'vue-property-decorator';
import FirebaseAccount from '../classes/Account';

@Component
export default class Login extends Vue {
	private email: string = '';

	public get loading(): boolean {
		return StatusControl.isLoading;
	}

	private async sendReset() {
		try {
			await FirebaseAccount.sendPasswordResetEmail(this.email);
			StatusControl.setError(
				new CustomError(
					null,
					'Password reset email sent!',
					CustomError.Type.Info,
				),
			);
		} catch (error) {
			StatusControl.setError(error);
		}
	}
}
