import Vue from 'vue';
import Router from 'vue-router';

import Home from './home/Home.vue';
import Calendar from './calendar/Calendar.vue';
import Organizations from './organization/Organizations.vue';
import Login from './login/Login.vue';
import Signup from './login/Signup.vue';
import Reset from './login/Reset.vue';
import UserControl, { User } from './store/UserModule';
import { auth } from 'firebase';
import StatusControl from './store/StatusModule';
import CustomError from './classes/CustomError';
import CreateGroup from './organization/CreateGroup.vue';
import AccountPage from './account/AccountPage.vue';

Vue.use(Router);

// Auth guard - simply checks if the user logged in.
async function RequireAuth(to: any, from: any, next: any) {
	const localUser = localStorage.getItem('ORGnizer.localUser');
	if (auth().currentUser && localUser) {
		// If authenticated, continue to path requested
		const user = JSON.parse(localUser) as User;
		UserControl.setUser(user);
		next();
	} else {
		StatusControl.setError(
			new CustomError(
				null,
				'You must be signed in to access the this page!',
				CustomError.Type.Error,
				'name: ' + to.name + ' , path: ' + to.path,
			),
		);
		next('/login');
	}
}

// Configure routes
const routes = [
	{ path: '/', component: Home, name: 'Home Page' },
	{ path: '/login', component: Login, name: 'Login' },
	{ path: '/signup', component: Signup, name: 'Signup' },
	{ path: '/login/password_reset', component: Reset, name: 'Password Reset' },
	{ path: '/creategroup', component: CreateGroup, name: 'Create Group' },
	{
		path: '/accountpage',
		component: AccountPage,
		beforeEnter: RequireAuth,
		name: 'Account',
	},

	// Calendar
	{
		path: '/calendar',
		component: Calendar,
		name: 'Calendar',
		beforeEnter: RequireAuth,
		props: (route: any) => ({ query: route.query }),
	},

	// Organizations
	{
		path: '/organizations',
		component: Organizations,
		name: 'Organizations',
		beforeEnter: RequireAuth,
	},

	// Default
	{ path: '*', redirect: '/' },
];

export default new Router({
	mode: 'history',
	linkActiveClass: 'active',
	routes,
});
