import '@babel/polyfill';
import 'bootstrap/dist/css/bootstrap.css';
import 'font-awesome/css/font-awesome.css';
import 'dayspan-vuetify/dist/lib/dayspan-vuetify.min.css'; // Import styling for dayspan calendar
import './app.scss';
import 'es6-shim';

import UserControl from './store/UserModule';

// Imported libraries
import Auth from 'firebase'; // Firebase -- auth and database
import Vue from 'vue'; // VueJs - frontend
import Vuex from 'vuex'; // Vuex -- state control
import App from './App.vue';
import DaySpanVuetify from 'dayspan-vuetify'; // Dayspan-Calendar: https://github.com/ClickerMonkey/dayspan-vuetify
import './plugins/vuetify'; // Vuetify -- pre-defined additions to VueJs

import store from './store';
import router from './router';

// Custom components
import AlertCmp from './components/shared/Alert.vue';
import CalendarCmp from './components/CalendarCmp.vue';
import EventDialog from './components/EventsDialog.vue';
import User from './store/UserModule';
import OrgAdminControl from './components/shared/OrgAdminControls.vue';
import Autocomplete from './components/shared/Autocomplete.vue';

Vue.use(Vuex);
Vue.use(DaySpanVuetify);
Vue.component('app-admin', OrgAdminControl);
Vue.component('app-alert', AlertCmp);
Vue.component('app-autocomplete', Autocomplete);
Vue.component('app-ds-calendar-app', CalendarCmp);
Vue.component('app-ds-event-dialog', EventDialog);

Auth.initializeApp({
	apiKey: 'AIzaSyAzVhQo-7J1mJbQiCvDB0oOk5kj9ZahQ_s',
	authDomain: 'orgnizer-64ab0.firebaseapp.com',
	databaseURL: 'https://orgnizer-64ab0.firebaseio.com',
	projectId: 'orgnizer-64ab0',
	storageBucket: 'orgnizer-64ab0.appspot.com',
});

let app: any;

Auth.auth().onAuthStateChanged((account) => {
	if (!app) {
		app = new Vue({
			el: '#app',
			render: (h) => h(App),
			store,
			router,
			// created() {},
		});
	}
	// If user state is not set AND firebase token is authenticated
	if (!UserControl.isUserSignedIn && account) {
		// this.$store.dispatch('signInactiveUserIn', user);
		User.signInactiveUserIn({ account });
	}
});
