






























import Vue from 'vue';
import { Component, Prop, Watch } from 'vue-property-decorator';

@Component
export default class Autocomplete extends Vue {
	@Prop({
		default: () => {
			return [];
		},
	})
	public autoCompleteUsers: Array<{
		name: string;
		avatar: string;
		uid: string;
	}>;

	@Prop({ default: 'Select' })
	public label: string;

	@Prop({ default: false })
	public clearInput: boolean;

	@Prop({ default: () => [() => true] })
	public rules: [() => boolean | string];

	private selectedUsers: string[] = [];

	private changed() {
		this.$emit('input', this.selectedUsers);
	}

	private remove(arg: any) {
		const index = this.selectedUsers.indexOf(arg.uid);
		if (index >= 0) {
			this.selectedUsers.splice(index, 1);
			this.changed();
		}
	}

	@Watch('clearInput')
	private clearAll(newVal: boolean) {
		if (newVal) {
			this.selectedUsers = [];
		}
	}
}
