















































































import Vue from 'vue';
import { Calendar, Weekday, Month, Event, Sorts, CalendarInput } from 'dayspan';
import UserControl from '../store/UserModule';
import { OrgStructure } from '../classes/OrgActions';
import StatusControl from '../store/StatusModule';
import CustomError from '../classes/CustomError';
import Firebase from 'firebase';
import FirebaseDB, { createPath as path } from '../classes/Database';

import OrgActions from '../classes/OrgActions';
import { Component, Watch } from 'vue-property-decorator';

interface CalendarData {
	busy: boolean;
	calendar: string;
	color: string;
	description: string;
	forecolor: string;
	icon: string;
	location: string;
	title: string;
}

@Component
export default class CustomCalendar extends Vue {
	public name = 'app';
	public storeKey = 'dayspanState';
	public calendar = Calendar.months<CalendarData, {}>();
	public readOnly = false;

	private dataRef: {
		[calendarName: string]: Firebase.database.Reference;
	} = {};
	private validOrgParams: string[] = [];

	private allCalendarEvents: {
		[calendarName: string]: Array<Event<CalendarData, {}>>;
	} = {};

	private userCalendars: string[] = [];
	private selectedCalendars: string[] = [];
	private selectAllCalendars = false;

	private wasRouted = false;

	public createEvent(arg: Event<CalendarData, {}>) {
		this.saveCalendar(arg.data.calendar);
	}

	@Watch('selectAllCalendars')
	public selectAllCalendarsClicked(override: boolean) {
		if (this.selectAllCalendars || this.selectedCalendars.length === 0) {
			Object.assign(this.selectedCalendars, this.userCalendars);
			this.selectedCalendars.push('My Calendar');
			this.selectAllCalendars = true;
		} else {
			this.selectedCalendars = [];
			this.selectAllCalendars = false;
		}
	}

	// Filters the currently displayed events by calendar type using the currently selected orgs
	@Watch('selectedCalendars')
	public async updateWatchedCalendars(newVal: string[]) {
		try {
			StatusControl.setLoading(true);
			// Filter only new additons
			const added = newVal.filter((calendarName) => {
				return !Object.keys(this.dataRef).includes(calendarName);
			});

			// Filter removed calendars
			const removed = Object.keys(this.dataRef).filter((calendarName) => {
				return !newVal.includes(calendarName);
			});

			// Add new
			for (const calendarName of added) {
				let ref;
				if (calendarName === '' || calendarName === 'My Calendar') {
					const user = UserControl.getUser;
					if (user) {
						ref = Firebase.database().ref(
							path('user', user.uid, 'calendarData'),
						);
					}
				} else {
					ref = Firebase.database().ref(
						path('org', calendarName, 'calendarData'),
					);
				}

				// Add listeners to new calendars
				if (ref) {
					this.dataRef[calendarName] = ref;
					FirebaseDB.on(ref, 'value', (data) => {
						if (data) {
							const calendarData = data.val();

							if (calendarData) {
								const calendarEvents = Calendar.fromInput<
									CalendarData,
									{}
								>(calendarData).events;

								// if (this.allCalendarEvents) {
								// 	this.allCalendarEvents[
								// 		calendarName
								// 	] = calendarEvents;

								this.replaceEventsOn(
									calendarName,
									calendarEvents,
								);
								// }
							}
						}
					});
				}
			}

			// Remove old
			for (const calendarName of removed) {
				this.dataRef[calendarName].off();
				delete this.dataRef[calendarName];

				// Filter
				let filteredEvents: any;
				if (calendarName === '' || calendarName === 'My Calendar') {
					filteredEvents = this.calendar.events.filter((event) => {
						return (
							event.data.calendar === calendarName ||
							event.data.calendar === '' ||
							event.data.calendar === 'My Calendar'
						);
					});
				} else {
					filteredEvents = this.calendar.events.filter((event) => {
						return event.data.calendar === calendarName;
					});
				}

				// Remove
				this.calendar.removeEvents(filteredEvents);
			}
		} catch (error) {
			StatusControl.setError(error);
		} finally {
			StatusControl.setLoading(false);
		}
	}

	// Runs when the page is mounted
	public async mounted() {
		try {
			StatusControl.setLoading(true);
			// Check if user is signed in
			if (!UserControl.getUser) {
				throw new CustomError(null, 'No user present');
			}

			this.userCalendars = await OrgActions.getUserOrgs();
			this.selectAllCalendars = true;

			// Handle url params
			await this.handleURLParams();
		} catch (error) {
			StatusControl.setError(new CustomError(error));
		} finally {
			StatusControl.setLoading(false);
		}
	}

	// Sets the calendar state
	public setState(state: any) {
		state.eventSorter = state.listTimes
			? Sorts.List([Sorts.FullDay, Sorts.Start])
			: Sorts.Start;

		this.calendar.set(state);
	}

	// Built in listener: Called before page is destroyed
	public beforeDestroy() {
		for (const ref of Object.keys(this.dataRef)) {
			FirebaseDB.off(ref);
			delete this.dataRef[ref];
		}
	}

	// Handles url parameters passed
	// Example: {root}/calendar?org=OrgName&org=Another name  -> would filter to calendars of these two names
	private async handleURLParams() {
		// Handle url parameters
		if (!this.$route.query.org) {
			return; // No params to parse
		}

		this.wasRouted = true;
		const orgParams = this.$route.query.org.toString().split(',');

		// If org does not exist, push it to nonvalid array
		const nonValidOrgs: string[] = [];
		for (const orgName of orgParams) {
			if (!(await OrgActions.exists(orgName))) {
				nonValidOrgs.push(orgName);
			}
		}

		// Filter only valid orgnames
		this.validOrgParams = orgParams.filter(
			(x) => !nonValidOrgs.includes(x),
		);

		Object.assign(this.selectedCalendars, this.validOrgParams);

		// Set error if an invalid org was found
		if (nonValidOrgs.length > 0) {
			StatusControl.setError(
				new CustomError(
					null,
					'Some orgs did not exist: [' +
						nonValidOrgs.toString() +
						']',
					CustomError.Type.Warning,
				),
			);
		}
	}

	private getCalendarTime(calendarEvent: any) {
		const sa = calendarEvent.start.format('a');
		const ea = calendarEvent.end.format('a');
		let sh = calendarEvent.start.format('h');
		let eh = calendarEvent.end.format('h');
		if (calendarEvent.start.minute !== 0) {
			sh += calendarEvent.start.format(':mm');
		}
		if (calendarEvent.end.minute !== 0) {
			eh += calendarEvent.end.format(':mm');
		}
		return sa === ea ? sh + ' - ' + eh + ea : sh + sa + ' - ' + eh + ea;
	}

	private replaceEventsOn(
		calendarName: string,
		events: Array<Event<CalendarData, {}>>,
	) {
		// Filter only selected events
		const filteredEvents = this.calendar.events.filter((event) => {
			if (calendarName === '' || calendarName === 'My Calendar') {
				return (
					event.data.calendar === calendarName ||
					event.data.calendar === '' ||
					event.data.calendar === 'My Calendar'
				);
			} else {
				return event.data.calendar === calendarName;
			}
		});

		// Remove events
		this.calendar.removeEvents(filteredEvents);

		// Replace them
		this.calendar.addEvents(events);
	}

	// Saves the calendar events related to this provided name to the correct place
	private async saveCalendar(calendarName: string) {
		try {
			StatusControl.setLoading(true);

			// Filter data
			const calendarEvents = this.calendar.events.filter((event) => {
				return event.data.calendar === calendarName;
			});

			// Convert data to plain text
			const dataToSave = this.calendarToPlainText(calendarEvents);

			// Save data to database
			if (calendarName === '' || calendarName === 'My Calendar') {
				const user = UserControl.getUser;
				if (!user) {
					throw new CustomError(null, 'No user signed in.');
				}

				// Save to personal calendar
				await FirebaseDB.set(
					path('user', user.uid, 'calendarData'),
					dataToSave,
				);
			} else {
				// Save to org calendar
				await FirebaseDB.set(
					path('org', calendarName, 'calendarData'),
					dataToSave,
				);
			}
		} catch (error) {
			StatusControl.setError(error);
		} finally {
			StatusControl.setLoading(false);
		}
	}

	// Checks if the loading state is set
	get loading() {
		return StatusControl.isLoading;
	}

	// Converts the calendar to plain text, with optionally overriding the events saved
	private calendarToPlainText(
		events?: Array<Event<CalendarData, {}>>,
	): CalendarInput<CalendarData, {}> {
		const out: CalendarInput<CalendarData, {}> = {};
		out.type = this.calendar.type;
		out.size = this.calendar.size;
		out.fill = this.calendar.fill;
		out.minimumSize = this.calendar.minimumSize;
		out.repeatCovers = this.calendar.repeatCovers;
		out.listTimes = this.calendar.listTimes;
		out.eventsOutside = this.calendar.eventsOutside;
		out.updateRows = this.calendar.updateRows;
		out.updateColumns = this.calendar.updateColumns;
		out.around = this.calendar.span.start.time;
		out.events = [];

		if (events) {
			for (const event of events) {
				const plainEvent: any = {};
				if (event.id) {
					plainEvent.id = event.id;
				}
				if (event.data) {
					plainEvent.data = event.data;
				}
				if (!event.visible) {
					plainEvent.visible = event.visible;
				}
				plainEvent.schedule = event.schedule.toInput();
				const meta = plainEvent.schedule.meta;
				if (meta) {
					for (const identifier of meta) {
						meta[identifier] = meta[identifier];
					}
				}
				out.events.push(plainEvent);
			}
		}

		return out;
	}
}
