import { database } from 'firebase';
import { OrgDefaultLogo, OrgStructure } from './OrgActions';

export interface FirebaseDatabaseModule {
	createPath(...args: string[]): string;
	get(
		path: string | database.Reference,
		value: any,
		valueType?: string,
	): Promise<any>;
	set(path: string | database.Reference): Promise<any>;
	update(path: string | database.Reference, value: any): Promise<any>;
	push(path: string | database.Reference, value: any): Promise<string>;
	exists(path: string | database.Reference): Promise<boolean>;
	remove(path: string | database.Reference): Promise<any>;
	on(
		path: string | database.Reference,
		valueType: database.EventType,
		callback: (
			a: database.DataSnapshot | null,
			b?: string | undefined,
		) => any,
	): any;
	off(path: string | database.Reference): any;
	convertResultToUser(arg: any): UserStructure;
	convertResultTOrg(arg: any): OrgStructure;
}

export function createPath(...args: string[]) {
	let output = '';

	for (let i = 0; i < args.length; i++) {
		if (i > 0) {
			output += '/';
		}

		output += args[i];
	}

	return output;
}

export interface UserStructure {
	calendarData: {};
	i: {
		first: string;
		last: string;
		img: string;
	};
	orgs: {
		[orgName: string]: boolean;
	};
	meta: {
		isAdmin: boolean;
	};
}

export default abstract class FirebaseDatabase {
	// Gets data from database
	public static async get(
		path: string | database.Reference,
		valueType: database.EventType = 'value',
	): Promise<any> {
		if (typeof path !== 'string') {
			return (await path.once(valueType)).val();
		} else {
			return (await database()
				.ref(path)
				.once(valueType)).val();
		}
	}

	// Sets data in database, overwriting if it already exists
	public static async set(
		path: string | database.Reference,
		value: any,
	): Promise<any> {
		if (typeof path !== 'string') {
			return await path.set(value);
		} else {
			return await database()
				.ref(path)
				.set(value);
		}
	}

	// Attempts to update database values with as little change as necessary
	public static async update(
		path: string | database.Reference,
		value: any,
	): Promise<any> {
		if (typeof path !== 'string') {
			return await path.update(value);
		} else {
			return await database()
				.ref(path)
				.update(value);
		}
	}

	// Pushes data to the path provided at a unique ID folder. Returns this ID or null if failed
	public static async push(
		path: string | database.Reference,
		value: any,
	): Promise<string | null> {
		if (typeof path !== 'string') {
			return path.push(value).key;
		} else {
			return database()
				.ref(path)
				.push(value).key;
		}
	}

	// Determines if data exists at a path
	public static async exists(
		path: string | database.Reference,
	): Promise<boolean> {
		if (typeof path !== 'string') {
			return (await path.once('value')).exists();
		} else {
			return (await database()
				.ref(path)
				.once('value')).exists();
		}
	}

	// Attempts to remove data at a path
	public static async remove(
		path: string | database.Reference,
	): Promise<any> {
		if (typeof path !== 'string') {
			return await path.remove();
		} else {
			return await database()
				.ref(path)
				.remove();
		}
	}

	// Sets a listener at a path and calls the callback provided when data is changed at this location
	public static on(
		path: string | database.Reference,
		valueType: database.EventType,
		callback: (
			a: database.DataSnapshot | null,
			b?: string | undefined,
		) => any,
	): any {
		if (typeof path !== 'string') {
			return path.on(valueType, callback);
		} else {
			return database()
				.ref(path)
				.on(valueType, callback);
		}
	}

	// Stops listening at the given path for data changes
	public static off(path: string | database.Reference): any {
		if (typeof path !== 'string') {
			return path.off();
		} else {
			return database()
				.ref(path)
				.off();
		}
	}

	public static convertResultToUser(arg: any): UserStructure {
		const user: UserStructure = {
			calendarData: arg.calendarData ? arg.calendarData : {},
			i: {
				first: arg.i ? (arg.i.first ? arg.i.first : '') : '',
				last: arg.i ? (arg.i.last ? arg.i.last : '') : '',
				img: arg.i
					? arg.i.img
						? arg.i.img
						: OrgDefaultLogo
					: OrgDefaultLogo,
			},
			orgs: arg.orgs ? arg.orgs : {},
			meta: {
				isAdmin: arg.meta
					? arg.meta.isAdmin
						? arg.meta.isAdmin
						: false
					: false,
			},
		};

		return user;
	}

	public static convertResultToOrg(arg: any): OrgStructure {
		const org: OrgStructure = {
			admins: arg.admins ? arg.admins : {},
			members: arg.members ? arg.members : {},
			meta: {
				publicEvents: arg.meta
					? arg.meta.publicEvents
						? arg.meta.publicEvents
						: false
					: false,
				logoSrc: arg.meta
					? arg.meta.logoSrc
						? arg.meta.logoSrc
						: OrgDefaultLogo
					: OrgDefaultLogo,
				description: arg.meta
					? arg.meta.description
						? arg.meta.description
						: ''
					: '',
			},
		};

		return org;
	}
}
