import { auth } from 'firebase';
import FirebaseDB, { createPath as path } from '../classes/Database';
import StatusControl from '../store/StatusModule';
import CustomError from '../classes/CustomError';
import { User } from '../store/UserModule';
import { OrgDefaultLogo } from './OrgActions';

export default abstract class FirebaseAccount {
	public static allowClassToSetLoading = true;

	public static async signInWithEmailAndPassword(
		email: string,
		password: string,
	): Promise<User> {
		try {
			this._loading(true);
			const response = await auth().signInWithEmailAndPassword(
				email,
				password,
			);

			// Check if user returned is valid
			if (!response.user) {
				this._loading(false);
				throw new CustomError(null, 'User is invalid');
			}

			// Attempt to get user information
			const userInfo = await this.getAccountInfo();

			const user: User = Object.assign(new User(), {
				uid: response.user.uid,
				firstName: userInfo.first,
				lastName: userInfo.last,
				isAdmin: await this.isUserAdmin(),
				img: userInfo.img,
			});

			return user;
		} catch (error) {
			throw error;
		} finally {
			this._loading(false);
		}
	}

	// Gets the current account info
	public static async getAccountInfo(): Promise<{
		first: string;
		last: string;
		img: string;
	}> {
		try {
			this._loading(true);

			// If current Firebase user is not valid, throw error
			const currentUser = auth().currentUser;
			if (!currentUser) {
				throw new CustomError(null, 'No signed in user.');
			} else {
				// Retrieve data from server
				const data = await FirebaseDB.get(
					path('user', currentUser.uid, 'i'),
				);

				return {
					first: data.first,
					last: data.last,
					img: data.img ? data.img : OrgDefaultLogo,
				};
			}
		} catch (error) {
			throw error;
		} finally {
			this._loading(false);
		}
	}

	public static async isUserAdmin(): Promise<boolean> {
		try {
			this._loading(true);
			const currentUser = auth().currentUser;
			if (!currentUser) {
				throw new CustomError(null, 'No signed in user.');
			} else {
				// Retrieve data from server
				const data = await FirebaseDB.get(
					path('user', currentUser.uid, 'meta/isAdmin'),
				);
				if (!data) {
					return false;
				}

				return true;
			}
		} catch (error) {
			throw error;
		} finally {
			this._loading(false);
		}
	}

	// Signs user out
	public static async signUserOut() {
		this._loading(true);
		await auth().signOut();
		this._loading(false);
	}

	public static async sendPasswordResetEmail(email: string) {
		try {
			this._loading(true);
			await auth().sendPasswordResetEmail(email);
		} catch (error) {
			throw error;
		} finally {
			this._loading(false);
		}
	}

	private static async _loading(val: boolean) {
		if (this.allowClassToSetLoading) {
			StatusControl.setLoading(val);
		}
	}
}
