












































































































































import Vue from 'vue';
import { Component } from 'vue-property-decorator';
import OrgAction, {
	OrgStructure,
	OrgAccessLevel,
	OrgAnnouncementStructure,
} from '../classes/OrgActions';
import FirebaseDB, { UserStructure } from '../classes/Database';
import UserControl, { User, UserModule } from '../store/UserModule';
import StatusControl from '../store/StatusModule';
import CustomError from '../classes/CustomError';
import { database, UserInfo } from 'firebase';
import FirebaseAccount from '../classes/Account';

@Component
export default class Organizations extends Vue {
	// Data
	private input: string = '';
	private promptVisible: boolean = false;
	private addDialog: boolean = false;
	private delDialog: boolean = false;
	private addUserDialog: boolean = false;

	private dataListeners: firebase.database.Reference[] = [];

	private selectedOrg: string = 'Select an Organization on the left to begin';

	private prompt = {
		promptQuestion: '',
		promptLabels: {
			promptConfirm: 'Ok',
			promptCancel: 'Cancel',
		},
		promptCallback: () => {},
	};

	// Will contain all orgs and their info
	private allOrgs: { [orgName: string]: OrgStructure } = {}; // Organizations the current user is associated with
	private allUsers: { [uid: string]: UserStructure } = {}; // Populated if the current user is an admin
	private allUsersInAutocompleteFormat: any = [];
	private accessLevels: string[] = [];

	// Will contain all user org names
	private userOrgs: any = {};

	private items: Array<{
		title: string;
		click: (context: Organizations) => any;
	}> = [
		{
			title: 'Add New Organization',
			click: (context) => {
				context.addDialog = true;
			},
		},
		{
			title: 'Delete Organization',
			click: (context) => {
				context.delDialog = true;
			},
		},
		{
			title: 'Set user membership of an Organization',
			click: (context) => {
				context.addUserDialog = true;
			},
		},
	];

	// Testing announcement creation
	private async addAnouncementTest() {
		const user = UserControl.getUser;
		if (!(await OrgAction.exists(this.selectedOrg))) {
			StatusControl.setError(
				new CustomError(
					null,
					'Organization: (' + this.selectedOrg + ') does not exist',
				),
			);
			return;
		} else if (!user) {
			StatusControl.setError(new CustomError(null, 'No user signed in.'));
			return;
		}

		// Example announcement
		const announcement: OrgAnnouncementStructure = {
			message: 'hello members.',
			meta: {
				timestamp: new Date().toLocaleString('en-us'),
				author: user.uid,
			},
		};

		// Test adding an announcement
		await OrgAction.addAnnouncement(announcement, this.selectedOrg);

		// Test getting all announcements
		console.log(
			"Announcements for '" + this.selectedOrg,
			await OrgAction.getAnnouncements(this.selectedOrg),
		);
	}

	private orgClicked(item: string) {
		this.selectedOrg = item;
	}

	private callClicked(func: (context: Organizations) => any) {
		func(this);
	}

	private async mounted() {
		try {
			StatusControl.setLoading(true);

			// Enable listener on all org changes
			let listener = database().ref('org');
			FirebaseDB.on(listener, 'value', (val) => {
				if (val) {
					const allOrgs = val.val();
					for (const currOrg of Object.keys(allOrgs)) {
						const org = FirebaseDB.convertResultToOrg(
							allOrgs[currOrg],
						);
						this.allOrgs[currOrg] = org;
					}
				}
			});
			this.dataListeners.push(listener);

			const currUser = UserControl.getUser;
			if (currUser) {
				// Enable listener on all current user orgs
				if (currUser.isAdmin) {
					// Enable listener on all users for admin options
					listener = database().ref('user');
					FirebaseDB.on(listener, 'value', (val) => {
						if (val) {
							const allUsers = val.val();

							// Parse incoming user data to a fimiliar format
							for (const currID of Object.keys(allUsers)) {
								this.allUsers[
									currID
								] = FirebaseDB.convertResultToUser(
									allUsers[currID],
								);
							}

							const autocompleteFormat = [];

							autocompleteFormat.push({ header: 'Users' });
							for (const currID of Object.keys(this.allUsers)) {
								const user = this.allUsers[currID];
								autocompleteFormat.push({
									name: user.i.first + ' ' + user.i.last,
									uid: currID,
									avatar: user.i.img,
								});
							}
							this.allUsersInAutocompleteFormat = autocompleteFormat;

							this.userOrgs = this.allUsers[currUser.uid].orgs;
						}
					});
				} else {
					// Enable listener on only current user orgs
					listener = database().ref('user/' + currUser.uid + '/orgs');
					FirebaseDB.on(listener, 'value', (val) => {
						if (val) {
							this.userOrgs = val.val();
						}
					});
				}
			}

			for (const i of Object.values(OrgAccessLevel)) {
				this.accessLevels.push(i);
			}
			this.dataListeners.push(listener);
		} catch (error) {
			StatusControl.setError(new CustomError(error));
		} finally {
			StatusControl.setLoading(false);
		}
	}

	private beforeDestroy() {
		// Disable all listeners
		for (const i of this.dataListeners) {
			i.off();
		}
	}

	private get userIsAdmin(): boolean {
		return UserControl.isUserAdmin;
	}

	private async addSaved(
		save: boolean,
		data: {
			orgName: string;
			orgData: OrgStructure;
		},
	) {
		if (save) {
			StatusControl.setLoading(true);
			await OrgAction.add(data.orgName, false, data.orgData);
			StatusControl.setLoading(false);
		}

		this.addDialog = false;
	}

	private async delSaved(save: boolean, data: { orgName: string }) {
		if (save) {
			StatusControl.setLoading(true);
			await OrgAction.deleteOrg(data.orgName);
			StatusControl.setLoading(false);
		}

		this.delDialog = false;
	}

	private async userSaved(
		save: boolean,
		data: {
			orgName: string;
			accessLevel: OrgAccessLevel;
			selectedUsers: string[];
		},
	) {
		if (save) {
			StatusControl.setLoading(true);
			for (const i of data.selectedUsers) {
				await OrgAction.addUserToOrg(data.orgName, i, data.accessLevel);
			}
			StatusControl.setLoading(false);
		}

		this.addUserDialog = false;
	}
}
