enum ErrorType {
	Info,
	Warning,
	Error,
}

export default class CustomError {
	public static readonly Type = ErrorType;
	public static TypeToString(type: ErrorType): string {
		switch (type) {
			case ErrorType.Info:
				return 'info';
			case ErrorType.Warning:
				return 'warning';
			case ErrorType.Error:
				return 'error';
		}
	}

	public stack?: string;
	public message: string;
	public type: ErrorType;

	constructor(
		error: Error | CustomError | null,
		message?: string,
		type: ErrorType = ErrorType.Error,
		stack?: any,
	) {
		if (error instanceof CustomError) {
			this.type = error.type;
		} else {
			this.type = type;
		}

		this.stack = stack ? stack : error ? error.stack : '';
		this.message = message ? message : error ? error.message : '';
	}
}
